/* base */
@import "./base/vars.scss";
@import "./base/typography.scss";
@import "./base/grid.scss";
@import "./base/ui.scss";

/* layout */
@import "./layout/inc-header.scss";
@import "./layout/inc-footer.scss";

/* pages */
@import "index";

* {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  min-height: 100%;
  height: 100%;
  font-size: 16px;
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
  background: linear-gradient(360deg, #fff 0%, #ffe8d9 100%);
  background-attachment: fixed;
  scrollbar-width: thin;          /* Firefox */
  scrollbar-color: #A8A8A8 transparent; /* Firefox: ползунок и трек */

  &::-webkit-scrollbar {
    width: 4px; /* Ширина вертикального скроллбара */
  }

  &::-webkit-scrollbar-track {
    background: transparent; /* Цвет фона */
  }

  /* Ползунок скроллбара */
  &::-webkit-scrollbar-thumb {
    background: #A8A8A8; /* Цвет ползунка */
    border-radius: 10px; /* Закругление углов */
  }
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100%;
  font-family: $baseFont;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.4;
  color: #000;
  // background-color: #fff;
}

#wrapper {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  min-height: 100%;
  min-width: 100%;
}

#middle {
  flex: 1 0 auto;
  -webkit-box-flex: 1;
}

#footer {
  flex: 0 0 auto;
  -webkit-box-flex: 0;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: $widthContainer;
  padding-left: $paddingContainer;
  padding-right: $paddingContainer;

  // @media(max-width: 1024.99px) {
  //   padding-left: 20px;
  //   padding-right: 20px;
  // }
}